@font-face {
  font-family: "SF Pro Display";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/sf-pro-display-regular.woff2") format("woff2"), url("../fonts/sf-pro-display-regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/sf-pro-display-bold.woff2") format("woff2"), url("../fonts/sf-pro-display-bold.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 800;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/sf-pro-display-heavy.woff2") format("woff2"), url("../fonts/sf-pro-display-heavy.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/sf-pro-text-regular.woff2") format("woff2"), url("../fonts/sf-pro-text-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-width: size(320px);
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  font-family: "SF Pro Display", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #2c3244;
  background-image: url("/img/4.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
}
@media (min-resolution: 1.5dppx) {
  body {
    background-image: url("/img/4@2x.png");
  }
}
@media (min-resolution: 3dppx) {
  body {
    background-image: url("/img/4@3x.png");
  }
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.btn {
  display: inline-flex;
  padding: 19px 25px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn--price {
  display: block;
  width: 280px;
  min-height: 54px;
  margin: 16px 0 0 10px;
  font-weight: 700;
  font-size: 17px;
  line-height: 17px;
  text-align: center;
  background: linear-gradient(270deg, #1987FF 0%, #19CCFF 100%);
  border-radius: 30px;
}

.page-content {
  width: 100%;
  height: 100%;
  padding-top: 151px;
}
@media (max-width: 375px) {
  .page-content {
    margin: auto 0 0;
    padding: 0;
  }
}

.page-content__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-content__wrapper h1 {
  margin: 0 2px 0 0;
  font-size: 36px;
  line-height: 43px;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.page-content__list {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 10px;
  padding: 31px 0 0;
  list-style-type: none;
}

.page-content__item {
  font-family: "SF Pro Text", "Arial", sans-serif;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: -0.321111px;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.684495);
}

.page-content__link {
  display: flex;
  align-items: center;
}

.page-content__icon {
  display: block;
  margin-right: 8px;
}
.page-content__icon--export {
  margin-right: 12px;
  margin-left: 2px;
}
.page-content__icon--noAds {
  margin-right: 9px;
  margin-left: 3px;
}

.page-content__price {
  padding-top: 45px;
}

.page-content__price-list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.page-content__price-item {
  margin-right: 9px;
}
.page-content__price-item:last-child {
  margin: 0;
}

.page-content__inner {
  position: relative;
  width: 137px;
  height: 162px;
  padding: 23px 12px 13px 8px;
  text-align: center;
  opacity: 0.49;
}

.page-content__price-inner h3 {
  margin: 0 0 16px;
  font-size: 17px;
  line-height: 20px;
}

.page-content__price-cost {
  display: flex;
  flex-direction: column;
  font-weight: 700;
  font-size: 22px;
  line-height: 15px;
}

.page-content__price-cost span {
  margin-bottom: 13px;
  padding-top: 3px;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
}

.page-content__price-text {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 20px;
  font-weight: 800;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
}
.page-content__price-text::before {
  content: "";
  position: absolute;
  top: 0;
  left: -11px;
  z-index: -1;
  width: 137px;
  height: 20px;
  background-color: #495795;
}

.page-content__price-inner > span {
  display: block;
  margin: 10px 0 0;
  font-size: 12px;
  line-height: 14px;
}

.page-content__price-mark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 131px;
  height: 162px;
  background-image: url("../img/svg/shape-1.svg");
  background-size: 131px 162px;
  background-repeat: no-repeat;
}

.page-content__price-mark--sale::before {
  background-image: url("../img/svg/shape-2.svg");
}

.page-content__inner--sale::before {
  content: "-83%";
  position: absolute;
  top: 0;
  right: 6px;
  width: 30px;
  height: 30px;
  padding: 9px 0 0;
  font-weight: 800;
  font-size: 10px;
  line-height: 12px;
  border-radius: 50%;
  background-color: #db0000;
}

input[type=radio]:checked + .page-content__inner {
  width: 151px;
  height: 178px;
  padding: 25px 15px 14px 8px;
  opacity: 1;
}
input[type=radio]:checked + .page-content__inner .page-content__price-mark::before {
  width: 145px;
  height: 178px;
  background-size: 145px 178px;
}
input[type=radio]:checked + .page-content__inner h3 {
  margin: 0 0 22px;
  font-size: 19px;
  line-height: 22px;
}
input[type=radio]:checked + .page-content__inner .page-content__price-cost {
  font-size: 24px;
}
input[type=radio]:checked + .page-content__inner .page-content__price-cost span {
  margin-bottom: 14px;
  font-size: 15px;
}
input[type=radio]:checked + .page-content__inner .page-content__price-text {
  height: 22px;
  font-size: 15px;
  line-height: 18px;
}
input[type=radio]:checked + .page-content__inner .page-content__price-text::before {
  left: -11px;
  width: 151px;
  height: 22px;
}
input[type=radio]:checked + .page-content__inner .page-content__price-text + span {
  margin: 11px 0 0;
  font-size: 13px;
  line-height: 16px;
}

input[type=radio]:checked + .page-content__inner--sale::before {
  top: -3px;
  right: 4px;
  width: 38px;
  height: 38px;
  padding: 11px 0 0;
  font-weight: 800;
  font-size: 12px;
  line-height: 14px;
}

.main-header {
  width: 375px;
  min-height: 24px;
  padding-top: 40px;
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.main-header__link {
  display: block;
  font-family: "SF Pro Text", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  opacity: 0.6;
  transition: color 0.3s ease;
  letter-spacing: -0.216667px;
}

.main-header__link--close {
  position: relative;
  width: 24px;
  height: 24px;
  fill: #ffffff;
  opacity: 0.4;
  transition: fill 0.3s ease;
}

.main-header__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-footer {
  position: relative;
  width: 375px;
  height: 100%;
  padding: 15px 0 38px;
}
.main-footer::after {
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  width: 134px;
  height: 5px;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  border-radius: 100px;
}

.main-footer__wrapper {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  mix-blend-mode: normal;
  opacity: 0.5;
}

.main-footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 4px 0 7px;
  text-decoration-line: underline;
  color: #bdc0d0;
}